import React from 'react';
import logo from './logo.svg';
import './App.css';

import { styled } from '@mui/material/styles';
import { Button, CircularProgress, Grid, LinearProgress, Typography } from '@mui/material';
import { StandardTextFieldProps, TextField } from '@mui/material';


import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';

import { AbiItem, TransactionReceipt } from 'ethereum-abi-types-generator';

import {ContractContext} from './assets/abi/voting-contract';
import Web3 from "web3";
import WalletConnectProvider from "@walletconnect/web3-provider";
import copy from 'clipboard-copy';
import { formatEthFromWei, formatNumber } from './Formatters';

//  Global setup Web3 Contract-Interaction
//
import abi from "./assets/abi/voting-contract.json";
import TopAppBar from './components/AppBar';
import ConnectButton from './components/ConnectButton';

import taxMeterImg from './assets/img/tax-meter.png'
import {ReactComponent as TelegramIcon} from './assets/img/Telegram_logo.svg';
import SendIcon from '@mui/icons-material/Send';

import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';



const contractAddress: string = '0x6B4E65FB66F49655cBe8B4228319bB2c9E26aad8'; // release on mainnet

//window.chainIdExpected = 97;
window.chainIdExpected = 56;


declare global {
  interface Window {
    dataRefreshInterval: number,
    farm_contract: ContractContext;
    web3: Web3;
    walletConnectProvider: WalletConnectProvider,
    ethereum: any;
    contractAddress: string;
    contract_abi: AbiItem[];
    walletAddress: string;
    connected: boolean;
    refferalAddress: string; // the ref address that invited me
    chainId: number;
    chainIdExpected: number;
    bnbPrice: number;
  }
}

interface AppProps {
  style?: React.CSSProperties;
}


const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.h3,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

window.dataRefreshInterval = 3000;

class App extends React.Component<AppProps> {
  
  state = {
    radioValue: "",
    progress: false,
    walletAccepted: false,
    alreadyVoted: false,
    totalYES: 0,
    totalNO: 0,
    totalVotes: 0
  }

  baseState: any;
  _stateInterval: any;

  constructor(props: AppProps) {
    super(props);

    this.baseState = this.state;
    // properties


  }


  componentDidMount = () => { 
    window.contractAddress = contractAddress;
    window.contract_abi = abi as AbiItem[];


    this.checkState();

    if(this._stateInterval){
      clearInterval(this._stateInterval);
    }
    this._stateInterval = setInterval(this.checkState, window.dataRefreshInterval);

    //this.setModalOpen();
  }


  componentWillUnmount = () => { 
    
  }

  
  checkState = () => {  

    if(window.connected !== true){
      this.setState(this.baseState);
      return;
    }

    this.setState({
      yourAddress: window.walletAddress,
    });

    window.farm_contract.methods.getWalletStatus(window.walletAddress).call().then((result: boolean) => {
      //console.log(result);
      this.setState({
        walletAccepted: result
      });
    });


    window.farm_contract.methods.getUserAnswer(window.walletAddress).call().then((result: string) => {
      //console.log(result);
      this.setState({
        alreadyVoted: result != "0"
      });
    });

    window.farm_contract.methods.getTotalAnswers("1").call().then((resultYes: string) => {
      //console.log(result);
      let totalYES = parseInt(resultYes);


      window.farm_contract.methods.getTotalAnswers("2").call().then((resultNO: string) => {
        //console.log(result);
        let totalNO = parseInt(resultNO);

        this.setState({
          totalYES: (100 / (totalYES + totalNO) * totalYES),
          totalNO: (100 / (totalYES + totalNO) * totalNO),
          totalVotes: totalYES + totalNO
        });

        
      });
    });


  }

  onClick_Submit = () => {
    //alert(this.state.radioValue)
    let openValue = "";
    if(this.state.radioValue.toLowerCase() == "yes"){
      openValue = "1";
    }else if(this.state.radioValue.toLowerCase() == "no"){
      openValue = "2";
    }

    this.setState({
      progress: true
    });

    window.farm_contract.methods.vote(openValue).send({from: window.walletAddress}).then((response) => {
      this.setState({
        progress: false
      });
    }).catch((err:any) => {
      alert("transaction failed");
      this.setState({
        progress: false
      });
    });
  }

  handleRadioChange = (event: any, _value: string) => {
    this.setState({
      radioValue: _value
    })
  }

  //  Main Layout
  //
  render = () => {
    return (
      <div>
      <div className={"wrapper"}>
     <img className={"logo"} src={"https://piston-token.com/images/logo.svg"}/>
    <h2>PISTON RACE</h2>
    <h3>VOTING</h3>

    <div style={{display:(window.connected == true && this.state.walletAccepted == true)?"":"none"}}>

        
        <div>
          <p className="question">Should we implement a variable tax system?</p>
          <p>Please see the details <a href="#details">here</a></p>

          
        </div>
        <div className="poll-area" style={{display: this.state.alreadyVoted?"none":""}}>

          <FormControl variant="standard" fullWidth={true} disabled={this.state.progress}>
              <RadioGroup
                name="poll"
                /*value={value}*/
                onChange={this.handleRadioChange}
              >
                <FormControlLabel value="yes" control={<Radio />} label="Yes" style={{marginLeft: "0", marginRight: "0"}} />
                <FormControlLabel value="no" control={<Radio />} label="No" style={{marginLeft: "0", marginRight: "0"}}/>
              </RadioGroup>
            </FormControl>

          <Button disabled={this.state.progress || this.state.radioValue == ""} fullWidth={true} variant={"contained"} color={"primary"} sx={{my: 0, marginTop: "32px" }} size="large" onClick={this.onClick_Submit}><SendIcon style={{height: "20px", marginRight: "8px"}}></SendIcon>submit vote</Button>
        </div>

        <div style={{textAlign:"center", paddingTop: "16px", paddingBottom: "16px", display: this.state.alreadyVoted?"":"none"}}>
          <div style={{border: "1px solid white", borderRadius: "10px"}}>
            <div className='voteResult'>Yes</div>
            <div className='voteResult' style={{fontSize: "20pt"}}>{this.state.totalYES}%</div>
          </div>
          <br/>
          <div style={{border: "1px solid white", borderRadius: "10px"}}>
            <div className='voteResult'>No</div>
            <div className='voteResult' style={{fontSize: "20pt"}}>{this.state.totalNO}%</div>
          </div>

          <br/>
          <div>Total Votes: {this.state.totalVotes}</div>
        </div>
    </div>

    <div style={{display:(window.connected == true && this.state.walletAccepted != true)?"":"none", border: "1px solid white", borderRadius: "10px"}}>
      <div className={"errormsgMain"}>your wallet is not invested in the piston race</div>
    </div>

    <Box sx={{ display: this.state.progress?'flex':'none' }}>
      <CircularProgress />
    </Box>

    <div style={{marginTop: "24px"}}>
      <p>For additional questions please join our <a href="https://t.me/PISTON_TOKEN"><TelegramIcon style={{ height: "16px", marginLeft: "8px", marginRight: "8px" }}></TelegramIcon>TELEGRAM GROUP</a></p>
    </div>

    <div style={{marginTop: "24px"}}>
      <ConnectButton />
    </div>
  </div>
  <a id="details"></a>
      <div className={"wrapper left"} style={{marginTop: "16px"}}>
        <h3>DETAILS</h3>
      After much deliberation and many community requests, we have decided to investigate the <b>possibility of implementing a change</b> to the mechanics of the Piston protocol.<br/>
<br/>
Since May, the protocol has been experiencing a consistent down trend in value. 
<br/>
<br/>
The recommended changes are for the greater good of the protocol and investors within. In addition to the use cases recently added, there are additional use cases on the horizon. Implementing this change will allow the protocol to achieve greater heights when these new use cases become active.
<br/><br/>
All of that said, here are the proposed changes to the system:
<br/><br/>
<h4>PISTON VARIABLE SELL TAX STRUCTURE</h4>

The system will have a <b>new taxing structure</b> implemented based on your <b>Claim Ratio.</b><br/><br/>

  There will be a <br/><br/>
  - <b>25% tax</b> incurred if your Claim Ratio is <b>higher than 3%</b> of your deposits.<br/><br/>

  - <b>50% tax</b> incurred if your Claim Ratio is <b>higher than 5%</b> of your deposits.<br/><br/>

  - <b>75% tax</b> incurred if your Claim Ratio is <b>higher than 7%</b> of your deposits.<br/><br/>

To be clear, your Claim Ratio goes up when you claim more. <br/><br/>

<h4>Example:</h4>

<b>Day 1</b> – Claim 1% (Claim Ratio + 1%. Claim Ratio now equals <b>1%</b>. Claim Tax is 10%)<br/><br/>

<b>Day 2</b> – Claim 1% (Claim Ratio + 1%. Claim Ratio now equals <b>2%</b>. Claim Tax is 10%)<br/><br/>

<b>Day 3</b> – Claim 1% (Claim Ratio + 1%. Claim Ratio now equals <b>3%</b>. Claim Tax is 10%)<br/><br/>

<b>Day 4</b> – Claim 1% (Claim Ratio + 1%. Claim Ratio now equals <b>4%</b>. Claim Tax is now 25%)<br/><br/>

<h4>Another Example:</h4>

<b>Day 1-3</b> – No action for 3 days. Accumulated 3% ROI.<br/><br/>

<b>Day 4</b> – Claim ROI accumulation of <b>4%</b>. (Claim Ratio + 4%. Claim Ratio now equals 4%. Claim Tax is 25%)<br/><br/>

<b>Day 5</b> – NITRO 1% (Claim Ratio – 1%. Claim Ratio now equals <b>3%</b>. Claim Tax lowered to 10%)<br/><br/>


<h4>So how do you lower your claim backlog?</h4>

NITROs, Airdrops and Deposits reduce your Claim Ratio. <br/><br/>

If you Nitro 1%, this will lower your Claim Ratio by 1%.<br/>
If you Airdrop 1%, this will lower your Claim Ratio by 1%.<br/>
If you Deposit an amount of PSTN to equal 1% of your gas tank, your Claim Ratio will be reduced by 1%.<br/>

Hopefully everyone will understand that proposing a change such as this to the protocol did not come easily. There was great internal discussion amongst the team.<br/><br/>

As we have always valued our investors' input and hold that paramount, we will be opening this proposed change up for a vote from the community. Voting will be held within the Piston dApp so we can get a valid representation of our Piston community.<br/>

Only votes from existing holders at the timestamp of this post will be counted.
<br/>
<br/>
<h3>How will this look?</h3>
<div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
<img style={{width: "100%", maxWidth: "520px"}} className={"logo"} src={taxMeterImg}/>
      </div>

      </div>
      </div>
    );
  }
}

export default App;
